<template>
  <div class="container">
    <div class="form">
      <query-form :inspect="inspect" ref="queryForm">
      </query-form>
    </div>
    <div class="option">
      <el-button type="primary" size="small" plain @click="inspectTable">检视</el-button>
      <el-button type="primary" size="small" plain  @click="exportTable">导出报表</el-button>
    </div>
    <div class="data">
      <div class="title">
        <el-alert
          title="统计截止月份：2018年3月 涉及保单范围：2018年1月1日 - 2018年3月21日"
          type="info"
          :closable="false">
        </el-alert>
      </div>
      <div class="table">
        <base-table
          :columns="columns"
          :data="bizData"
          row-key="id">
        </base-table>
      </div>
    </div>
  </div>
</template>

<script>
const QueryForm = () => import('./QueryForm')
const BaseTable = () => import('@/components/BaseTable')

export default {
  name: 'BizSector',
  components: {QueryForm, BaseTable},
  data () {
    return {
      inspect: {
        org: '50000',
        channel: 'ct',
        type: 'month',
        timeRange: '2018Q1',
        version: '50000201801010900'
      },
      columns: [
        {prop: 'bizName', width: 150, label: '个车分类生命表(业务板块)', align: 'center'},
        {
          prop: 'premiumIncome',
          width: 240,
          label: '保费收入',
          align: 'center',
          children: [
            {prop: 'realIncome', width: 120, label: '实际值(万元)', align: 'center'},
            {prop: 'incomePercent', width: 120, label: '占比(%)', align: 'center'}
          ]
        },
        {
          prop: 'operationCostRate',
          width: 120,
          label: '经营成本率',
          align: 'center',
          children: [
            {prop: 'expOperation', width: 120, label: '预算值(%)', align: 'center'},
            {prop: 'realOperation', width: 120, label: '实际值(%)', align: 'center'},
            {prop: 'disOperation', width: 120, label: '二者差(%)', align: 'center'}
          ]
        },
        {
          prop: 'marketCostRate',
          width: 120,
          label: '市场费用率',
          align: 'center',
          children: [
            {prop: 'expMarket', width: 120, label: '预算值(%)', align: 'center'},
            {prop: 'realMarket', width: 120, label: '实际值(%)', align: 'center'},
            {prop: 'disMarket', width: 120, label: '二者差(%)', align: 'center'}
          ]
        },
        {
          prop: 'finalPayRate',
          width: 120,
          label: '终极赔付率',
          align: 'center',
          children: [
            {prop: 'expFinal', width: 120, label: '预算值(%)', align: 'center'},
            {prop: 'realFinal', width: 120, label: '实际值(%)', align: 'center'},
            {prop: 'disFinal', width: 120, label: '二者差(%)', align: 'center'}
          ]
        }
      ],
      bizData: [
        {
          id: '1',
          bizName: '家庭自用客车',
          realIncome: '8647.78',
          incomePercent: '26.92',
          expOperation: '97.6',
          realOperation: '98.2',
          disOperation: '0.6',
          expMarket: '16.8',
          realMarket: '17.5',
          disMarket: '0.7',
          expFinal: '56.89',
          realFinal: '56.86',
          disFinal: '-0.03'
        },
        {
          id: '2',
          bizName: '非营业企业客车',
          realIncome: '5126.99',
          incomePercent: '15.96',
          expOperation: '97.9',
          realOperation: '97.7',
          disOperation: '-0.2',
          expMarket: '20.1',
          realMarket: '21.3',
          disMarket: '1.2',
          expFinal: '59',
          realFinal: '58.6',
          disFinal: '-0.4'
        },
        {
          id: '3',
          bizName: '营业客车',
          realIncome: '5033.83',
          incomePercent: '15.67',
          expOperation: '97.4',
          realOperation: '97.3',
          disOperation: '-0.1',
          expMarket: '17.2',
          realMarket: '18.5',
          disMarket: '1.3',
          expFinal: '57.2',
          realFinal: '57.6',
          disFinal: '0.4'
        },
        {
          id: '4',
          bizName: '非营业货车',
          realIncome: '2605.25',
          incomePercent: '8.11',
          expOperation: '97.6',
          realOperation: '98.2',
          disOperation: '0.6',
          expMarket: '16.8',
          realMarket: '17.5',
          disMarket: '0.7',
          expFinal: '56.89',
          realFinal: '56.86',
          disFinal: '-0.03'
        },
        {
          id: '5',
          bizName: '营业货车',
          realIncome: '2563.50',
          incomePercent: '7.98',
          expOperation: '97.9',
          realOperation: '97.7',
          disOperation: '-0.2',
          expMarket: '20.1',
          realMarket: '21.3',
          disMarket: '1.2',
          expFinal: '59',
          realFinal: '58.6',
          disFinal: '-0.4'
        },
        {
          id: '6',
          bizName: '特种车',
          realIncome: '2512.10',
          incomePercent: '7.82',
          expOperation: '97.4',
          realOperation: '97.3',
          disOperation: '-0.1',
          expMarket: '17.2',
          realMarket: '18.5',
          disMarket: '1.3',
          expFinal: '57.2',
          realFinal: '57.6',
          disFinal: '0.4'
        },
        {
          id: '7',
          bizName: '摩托车和拖拉机',
          realIncome: '5634.55',
          incomePercent: '17.54',
          expOperation: '97.6',
          realOperation: '98.2',
          disOperation: '0.6',
          expMarket: '16.8',
          realMarket: '17.5',
          disMarket: '0.7',
          expFinal: '56.89',
          realFinal: '56.86',
          disFinal: '-0.03'
        },
        {
          id: '8',
          bizName: '合计',
          realIncome: '32124',
          incomePercent: '100',
          expOperation: '97.62',
          realOperation: '97.86',
          disOperation: '0.24',
          expMarket: '17.68',
          realMarket: '18.64',
          disMarket: '0.96',
          expFinal: '57.47',
          realFinal: '57.45',
          disFinal: '-0.018'
        }
      ]
    }
  },
  methods: {
    inspectTable () {
    },
    exportTable () {
    },
    sum ({ columns, data }) {
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '合计'
        } else {
          sums[index] = 0
          data.forEach(row => {
            sums[index] += Number(row[column.property])
          })
          if (index >= 3) {
            sums[index] /= data.length
          }
          sums[index] = sums[index].toFixed(2)
        }
      })
      return sums
    }
  }
}
</script>

<style scoped>

</style>



